<template>
<div>
  <div>
     <a   @click="$emit('showPage', 'shop')"><span  class="uk-icon link-button subtitle" uk-icon="icon: arrow-left; ratio: 1;">Volver a la Tienda</span></a>
  </div>
    <div class="product-description">
  
      <span class="uk-text-large">{{product.title}}</span>

      <div class="uk-align-center">
        
        <div class="uk-card">
          <img :src="imagePrefix +product.code+'.jpg'" :alt="product.title">      
        </div>
      <div  class="uk-text-large bm-centered" style="display:block">${{product.price}}</div>
        <div class="uk-align-center"> 
  
            <button  @click="$emit('addToCart', product)" class="uk-button uk-button-primary uk-align-center">
              <!-- <span  class="uk-icon" uk-icon="icon: cart; ratio: 1.5;">Agregar al carrito</span> -->
              <span>Agregar al carrito</span>
            </button>
             <dt >Disponibles: {{product.stock}}</dt>
             <dl v-for='desc in descriptions' :key='desc.code' class="uk-description-list">
              <dt>{{desc.title}}</dt>
              <dd>{{desc.text}}</dd>
          </dl>

              <div class="bm-centered bm-margin-top">
                  <a  @click="$emit('showPage', 'shop')"><span  class="uk-icon link-button" uk-icon="icon: arrow-left; ratio: 1;">Volver a la Tienda</span></a>
              </div>

          </div>
      </div>
  </div>
</div>

</template>

<script>
import CONFIG from './../../../config.js'

export default {
  name: 'ProductDetail',
  props: {
     product : Object
  },
  data () {
          return {
              imagePrefix: CONFIG.IMAGES_PREFIX,
              descriptions: []
          }
  },
  methods: {
    parseDescription: function(){

      let sections = this.product.description.split(";")

      this.descriptions = []

      for (let i = 0; i < sections.length; i++){

        const titleDesc = sections[i].split(":");

        let descObject=  {id:i, title:titleDesc[0], text:titleDesc[1]};
    
         this.descriptions.push(descObject) 
        }

        // console.log("description")
        // console.log(this.descriptions)


    },
    
  },
  watch: {
    product: function() {
      // console.log("actualizando description")
      this.parseDescription();
    }
  }
}
  
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
  width: 400px ;
}
.subtitle{
  margin:20px;
}
.product-description{
  padding: 20px;
}
</style>